*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

:root {
  --text-main: "DM Sans", sans-serif;
  --text-secondary: "Titan One", cursive;
  --color-design-main: #d41443;
  --color-design-white: #fff;
  --color-design-black: #000;
  --color-subtitle: #ffa5ba;
  --color-description: #907e82;
  --color-line-in-contacts: #e1e1e1;
  --bg-section: #fff5f6;
  --bg-btn-dark: #d41443;
  --bg-btn-dark-hover: #c7133e;
  --bg-btn-light: #fff;
  --bg-btn-light-hover: #ecebeb;
  --bg-quote: #fafafa;
  --bg-social: #fea5bb;
  --bg-header-hero: #ffb8ca;
  --bg-mobile-menu: #e17992;
  --bg-beige-color: #f0d1a5;
  --bg-green-color: #c2e297;
}

.section-title {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--color-design-main);
  font-family: Titan One;
  font-size: 34px;
  line-height: 1.15;
}

@media screen and (min-width: 768px) {
