.btn-light {
  text-align: center;
  background-color: var(--bg-btn-light);
  cursor: pointer;
  color: var(--color-design-main);
  border: 0;
  align-items: center;
  transition: background-color .25s linear;
  display: flex;
}

.btn-light:hover, .btn-light:focus {
  background-color: var(--bg-btn-light-hover);
}

.btn-dark {
  text-align: center;
  background-color: var(--bg-btn-dark);
  cursor: pointer;
  color: var(--color-design-white);
  border: 0;
  align-items: center;
  transition: background-color .25s linear;
  display: flex;
}

.btn-dark:hover, .btn-dark:focus {
