:root {
  --white: #fff;
  --lightwhite: #fff5f6;
  --black: #000;
  --velvet: #d41443;
  --gray: #907e82;
}

.contact {
  background-image: linear-gradient(to bottom, var(--white) 25%, var(--lightwhite) 25%, var(--lightwhite) 70%);
  padding: 60px 0 40px;
}

.container {
  max-width: 425px;
  margin: 0 auto;
  padding: 0 15px;
}

.contact__content {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px 32px;
  padding-bottom: 32px;
  display: flex;
}

.contact__card {
  background-color: var(--white);
  background: var(--white);
  border-radius: 24px;
  padding: 40px 32px;
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.contact__info {
  border-bottom: 1px solid #e1e1e1;
  padding-bottom: 24px;
}

.contact__h3 {
  width: 80px;
  height: 16px;
  color: var(--white);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .48px;
  width: 80px;
  height: 16px;
  background: #f0d1a5;
  border-radius: 4px;
  flex-direction: column;
  flex-shrink: 0;
  justify-content: center;
  margin-bottom: 20px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
