}

.contact__h4 {
  color: var(--black);
  letter-spacing: .48px;
  margin-bottom: 8px;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 500;
  line-height: 186%;
}

.contact__p {
  color: var(--gray);
  letter-spacing: .48px;
  font-family: DM Sans;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
