.shop-hours {
  border-bottom: 1px solid #e1e1e1;
  flex-direction: column;
  gap: 8px;
  padding: 24px 0;
  list-style: none;
  display: flex;
}

.shop-hours__day {
  color: var(--black);
  font-size: 12px;
  font-weight: 500;
  line-height: 186%;
}

.shop-hours__time {
  color: #ffa5ba;
