.advantages {
    padding-bottom: 97px;
    background-color: var(--bg-section);

    @media screen and(min-width: 768px) {
        padding-top: 0;
        padding-bottom: 140px;
    }

    @media screen and(min-width: 1280px) {
        padding-bottom: 139px;
    }

    &-list {
        margin: -19px;

        @media screen and (min-width: 480px) {
            padding-left: 20px;
        }

        @media screen and(min-width: 768px) {
            padding-left: 0;
            display: flex;
            margin: -30px;
        }

        @media screen and(min-width: 1280px) {
            margin: -15px;
        }
    }

    &-item {
        width: calc((100% - 76px) / 3);
        margin: 19px;

        @media screen and(min-width: 768px) {
            width: calc((100% - 120px) / 3);
            margin: 30px;
        }

        @media screen and(min-width: 1280px) {
            width: calc((100% - 60px) / 3);
            margin: 15px;
        }
    }

    &-numbers {
        font-family: var(--text-secondary);
        font-size: 36px;
        line-height: 1.14;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        margin-bottom: 16px;
        color: var(--bg-btn-dark);

        @media screen and(min-width: 768px) {
            text-transform: none;
            font-size: 48px;
            line-height: 1.15;
            margin-bottom: 13px;
        }

        @media screen and(min-width: 1280px) {
            font-size: 58px;
        }
    }

    &-descriptions {
        font-weight: 700;
        color: var(--color-design-black);
        font-size: 12px;
        line-height: 1.7;
        width: 230px;

        @media screen and(min-width: 768px) {
            font-size: 14px;
            line-height: 1.74;
            margin-bottom: 13px;
            width: 180px;
        }

        @media screen and(min-width: 1280px) {
            font-size: 16px;
            line-height: 1.76;
            margin-bottom: 15px;
            width: 330px;
        }
    }
}

.advantages-item:first-child::before {
    content: ' ';
    display: block;
    background-image: url(/src/images/advantages-images/advantages-icon1.png);
    background-repeat: no-repeat;
    width: 50px;
    height: 50px;
    fill: var(--color-subtitle);
    margin-bottom: 16px;

    @media screen and(min-width: 768px) {
        margin-bottom: 36px;
    }

    @media screen and(min-width: 1280px) {
        margin-bottom: 40px;
    }
}

